@font-face {
  font-family: "Roboto";
  src: url("./Roboto.ttf"), format("ttf");
}

:root {

  --colors-green50: #F1FDF7;
  --colors-green100: #E8FCF1;
  --colors-green200: #DAFBE8;
  --colors-green300: #C8F9DD;
  --colors-green400: #ADF5CC;
  --colors-green500: #88F1B6;
  --colors-green600: #17C964;
  --colors-green700: #13A452;
  --colors-green800: #108944;
  --colors-green900: #06371B;
  --colors-gray25: #F8F9FA;
  --colors-gray50: #F1F3F5;
  --colors-gray100: #ECEEF0;
  --colors-gray200: #E6E8EB;
  --colors-gray300: #DFE3E6;
  --colors-gray400: #D7DBDF;
  --colors-gray500: #C1C8CD;
  --colors-gray600: #889096;
  --colors-gray700: #7E868C;
  --colors-gray800: #687076;
  --colors-gray900: #11181C;
  --colors-text: var(--colors-gray900);
  --colors-text-: var(--colors-gray800);
  --animation-jump: cubic-bezier(0.75, -0.1, 0.25, 1.25);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto";
  /* transition: all .25s ease; */
}

.Header {
  z-index: 1;
  position: absolute;
  top: 0px;
  height: 48px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  backdrop-filter: blur(4px) saturate(180%);
}

.Container {
  display: flex;
  padding-bottom: 0;
  width: 100%;
}

.Container-Title {
  display: inline-block;
  font-size: 48px;
  font-weight: 600;
  color: var(--colors-text);
}

.Any_More {
  padding: 0 4px;
  background: linear-gradient(0deg, var(--colors-green400), var(--colors-green300));
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: 0 100%;
  transition: all .375s var(--animation-jump);
}

.Any_More:hover {
  background-size: 100% 12%;
  cursor: pointer;
}

.Container-Text {
  display: inline-block;
  font-size: 18px;
  color: var(--colors-text);
  opacity: 0.75;
}

.MediasBtn {
  display: inline-flex;
  align-items: center;
  border-radius: 10px;
  padding: 6px 12px;
  background-color: var(--colors-gray50);
  margin-right: 12px;
  color: var(--colors-text);
  transition: all .25s ease;
}

.MediasBtn:hover {
  background-color: var(--colors-green600);
  color: var(--colors-gray100);
  cursor: pointer;
  User-select: none;
}

