.icos {
  display: inline-block;
}

.Search {
  color: var(--colors-text);
  padding: 12px;
  background-color: var(--colors-gray50);
  border: none;
  outline: none;
  height: 36px;
  width: 156px;
  border-radius: 10px;
  transition: all .5s var(--animation-jump);
}

.Go {
  display: inline-block;
  border-radius: 10px;
  padding: 8px 12px;
  background-color: var(--colors-gray50);
  margin-left: 8px;
  color: var(--colors-text);
  transition: all .25s ease;
}

.Go:hover {
  background-color: var(--colors-green600);
  color: var(--colors-gray100);
  cursor: pointer;
}

.Left {
  width: 412px;
  /* box-shadow: 0 0 12px rgba(63, 55, 54, 0.1); */
}

.Right {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  margin-left: 32px;
  width: calc(100% - 2vh - 412px);
  /* box-shadow: 0 0 12px rgba(63, 55, 54, 0.1); */
}

.More {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 16vh 12vw;
  top: 0;
  right: 0;
  bottom: 12vw;
  left: 0;
  margin: 0 auto;
  padding-bottom: 0;
  width: 100%;
  max-width: 1240px;
}

.Bottom {
  height: 148px;
  margin-top: 32px;
  border-radius: 16px;
  background-color: var(--colors-gray25);
  padding: 12px 24px;
overflow: hidden;
}

.Info {
  width: 100%;
  /* margin-top: 4px; */
  border-radius: 10px;
  height: calc((100% - 48px) / 6);
  transition: all .25s ease;
  padding: 8px 12px;
  color: var(--colors-gray600);
  white-space: nowrap;
}

.Info:hover {
  background-color: var(--colors-gray100);
  color: var(--colors-text);
  cursor: pointer;
}

.Card-Column-content .Info {
  padding: 6px 12px;
  height: calc((100% - 72px) / 6);
  margin-top: 8px;
}

.Bottom-title {
  display: inline-block;
  padding: 0 2px;
  color: var(--colors-text);
  font-size: 24px;
  font-weight: 550;
  background: linear-gradient(0deg, var(--colors-green400), var(--colors-green400));
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: 0 100%;
  white-space: nowrap;
}

.Bottom-historyBox {
  border-radius: 12px;
  margin-top: 4px;
  width: calc(100% + 12px);
  margin-left: -6px;
  height: calc(100% - 22px);
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: visible;
}

.HistoryBar {
  height: 80px;
  min-width: 256px;
  border-radius: 10px;
  padding: 8px 12px;
  transition: all .25s ease;
  margin-right: 64px;
}

.HistoryBar-title {
  display: inline-block;
  padding: 0 2px;
  color: var(--colors-text);
  font-size: 18px;
  font-weight: 400;
  background: linear-gradient(0deg, var(--colors-green400), var(--colors-green400));
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: 0 100%;
}

.HistoryBar-text {
  font-size: 14px;
  color: var(--colors-text);
  font-weight: 0;
  opacity: 0.75;
}

.HistoryBar:hover {
  background-color: var(--colors-gray100);
  cursor: pointer;
}

/*定义滚动条高宽及背景
 高宽分别对应横竖滚动条的尺寸*/
.Bottom-historyBox::-webkit-scrollbar {
  height: 8px;
}

/*定义滚动条轨道
  内阴影+圆角*/
.Bottom-historyBox::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: var(--colors-gray100);
}

/*定义滑块
  内阴影+圆角*/
.Bottom-historyBox::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: var(--colors-green500);
  
}

@media (max-width: 1024px) {
  .Container {
    flex-direction: column;
  }

  .Left {
    width: 100%;
  }

  .Right {
    margin-left: 0;
    margin-top: 32px;
    height: 300px;
    width: 100%;
  }
}

@media (min-width: 320px) {
  .Search:focus {
    width: 210px;
  }
}

@media (min-width: 1024px) {
  .More {
    padding: 16vh 6vw;
  }
}

@media (min-width: 1400px) {
  .More {
    padding: 16vh 3vw;
  }
}

@media (min-width: 1600px) {
  .More {
    padding: 16vh 0vw;
  }
}

@media (max-width: 820px) {
  .More {
    padding: 16vh 6vw;
  }
}