.Card {
   display: flex;
   padding: 16px;
   position: absolute;
   height: calc(50% - 12px);
   width: calc(50% - 12px);
   border-radius: 16px;
   background-color: var(--colors-gray25);
   transition: all .5s var(--animation-jump);
}

.Card-title {
   padding: 0 2px;
   color: var(--colors-text);
   font-size: 24px;
   font-weight: 550;
   background: linear-gradient(0deg, var(--colors-green400), var(--colors-green400));
   background-size: 100% 50%;
   background-repeat: no-repeat;
   background-position: 0 100%;
}

.Card-Column {
   position: absolute;
   height: calc(25% - 12px);
   width: 100%;
   border-radius: 16px;
   background-color: var(--colors-gray25);
   padding: 16px;
   transition: all .375s var(--animation-jump);
}

.CardBtn-Column {
   display: inline-block;
   position: absolute;
   right: 0;
   bottom: 0;
   padding: 6px 12px;
   background-color: var(--colors-gray100);
   color: var(--colors-text);
   border-radius: 10px;
   transition: all .25s ease;
   cursor: pointer;
   user-select: none;
}

.CardBtn-Column:hover {
   background-color: var(--colors-green600);
   color: var(--colors-gray50);
}

.CardBtn {
   display: inline-block;
   position: absolute;
   right: 0;
   bottom: 0;
   padding: 6px 12px;
   background-color: var(--colors-gray100);
   color: var(--colors-text);
   border-radius: 10px;
   transition: all .25s ease;
   cursor: pointer;
   user-select: none;
}

.CardBtn:hover {
   background-color: var(--colors-green600);
   color: var(--colors-gray50);
}

.Card-intro {
   display: block;
   font-size: 16px;
   color: var(--colors-text);
   font-weight: 0;
   opacity: 0.75;
   white-space: nowrap;
   overflow: hidden;
}

.Card-Column-title {
   padding: 0 2px;
   color: var(--colors-text);
   font-size: 24px;
   font-weight: 550;
   background: linear-gradient(0deg, var(--colors-green400), var(--colors-green400));
   background-size: 100% 50%;
   background-repeat: no-repeat;
   background-position: 0 100%;
   
}

.Card-Column-intro {
   font-size: 16px;
   color: var(--colors-text);
   font-weight: 0;
   margin-left: 8px;
   opacity: 0.75;
   white-space: nowrap;
}

.Card-content {
   width: 100%;
   height: 100%;
   position: relative;
}

.Card-Column-content {
   width: 100%;
   height: 100%;
   position: relative;
   overflow: hidden;
}